
body {
  margin: 0;
  background-color: white;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  text-align:center;
}
h4 {
  font-weight: 400;
  text-align: left;
}
h5 {
  font-weight: 400;
  text-align: left;
}
th, td {
  padding: 15px;
}
.micro {
  font-size: 10px
}
.small {
  font-size: 11px
}
.medium {
  font-size: 15px
}
a:hover, a:visited, a:link, a:active
{
    color:#000000;
    text-decoration: none;
}

/* top menu bar*/
.nav {
  margin-bottom: 1em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #2196F3;
  color: #fff;
  padding: 1em;
}
.nav > .main {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-align: left;
}
.nav >.right {
  font-size: 0.8em;
}
.item {
  font-weight: 500;
  padding: 0 0.5em;
  cursor: pointer;
}

/* Profile.vue */
.container {
  text-align: center;
}
.message {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}
.message-header {
  cursor: pointer;
}
.message-body   {
  padding: 0;
  max-height: 50em;
  width: 100%;
  overflow: hidden;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
}
.is-closed .message-body {
  max-height: 0;
}
.is-primary .arrow {
  -webkit-transform: rotateZ(90deg) translatex(-5px);
          transform: rotateZ(90deg) translatex(-5px);
}
.arrow {
  display: inline-block;
}
.message-content {
  padding: 20px;
}

/*ProfileMenu.vue*/
.form {
  display: inline-block;
  width: 320px;
}
inputRow {
  display: block;
  margin-top: 0.5em;
}
.input {
  width: 100%;
  font-size: 1em;
  padding: 0.5em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  border: none;
  border-bottom: 2px solid #2196F3;
}
.inputLabel {
  display: block;
  text-align: left;
}
.actionRow {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 0.5em;
  text-align: right;
}
.action {
  font-size: 1em;
  border: none;
  outline: none;
  padding: 12px 50px;
  background-color: #ededed;
  cursor: pointer;
}
.lineBreak {
  height: 0;
  border-bottom: 1px solid #ededed;
  margin: 1em 10%;
}

